@import url(https://cdn.rawgit.com/kattergil/NotoSerifKR-Web/76eb9ebf/stylesheet/NotoSerif-Web.css);

*{margin:0;padding:0;font:inherit;color:inherit;}
*, :after, :before {box-sizing:border-box;flex-shrink:0;}
:root {-webkit-tap-highlight-color:transparent;-webkit-text-size-adjust:100%;text-size-adjust:100%;cursor:default;line-height:1.5;overflow-wrap:break-word;-moz-tab-size:4;tab-size:4}
html, body {height:100%;}
img, picture, video, canvas, svg {display: block;max-width:100%;}
button {background:none;border:0;cursor:pointer;}
a {text-decoration:none}
table {border-collapse:collapse;border-spacing:0}

body {background-color:#fbd285;font-family:'Noto Serif KR', serif;font-weight:900;}
@media only screen and (min-width:800px) {
    #root {width:800px;margin:0 auto;}
}
#root {height:100%;}
.main {
    display:block;
    height:100%;
    background:transparent url(https://blog.kakaocdn.net/dn/c5RN2H/btrr159d2aP/v0GKeDnaCfba83aHJ8voIK/img.png) no-repeat fixed center;
    background-size:contain;
    text-indent:-9999em;
}
.wrap {
    height:100%;
    padding:3vh;
    display:grid;
    grid-template-columns:repeat(3, 1fr);
    grid-template-rows:repeat(4, 1fr);
}
.wrap > a {
    display:block;
    background-repeat: no-repeat;
    background-position: center;
    background-color: transparent;
    background-size:contain;
    cursor:pointer;
    text-indent:-9999em;
}
