.view {
    position:relative;
    height:100%;
}
.view .bg {
    position:relative;
    left:0;
    right:0;
    top:0;
    bottom:0;
    z-index:1;
    height:100%;
    background-repeat:no-repeat;
    background-position:center;
    background-size:cover;
    opacity:.1;
}
.view .content {
    position:absolute;
    left:0;
    right:0;
    top:0;
    z-index:2;
    margin:3em 1em 0 1.5em;
}
@media only screen and (min-width:800px) {
    .view {
        font-size:1.3em;
    }
}
.view h2 {
    padding:0 0 0 10px;
    border-left:2px solid #999;
}
.view .p {
    margin-top:2em;
    padding:0 0 0 10px;
    border-left:2px solid #999;
}
.view blockquote {
    display:inline;
    font-size:1.3em;
    line-height:1;
    color:darkred;
}
.view blockquote:before {
    content:open-quote;
    font-size:1.3em;
    margin-left:.1em;
    vertical-align:-.3em;
}
.view blockquote:after {
    content:close-quote;
    font-size:1.3em;
    vertical-align:-.3em;
}
.view ul {
    margin-top:2em;
    border-left:2px solid #999;
}
.view li {
    list-style:none;
    padding:15px 0 0 10px;
    font-size:.8em;
    line-height:1.5;
    letter-spacing:-1px;
    text-align:left;
}
.view li:first-child {
    padding-top:0;
}
.view .back {
    display:block;
    position:fixed;
    bottom:2em;
    right:2em;
    z-index:9999999;
    width:3em;
    height:3em;
    background:transparent url(https://upload.wikimedia.org/wikipedia/commons/b/b1/Back_Arrow.svg) no-repeat 0 0;
    background-size:contain;
    text-indent:-9999em;
}
